<template>
    <Doc>
        <H1>API</H1>
        <H2></H2>
        <P>new</P>
        <Code :code="`new MyHistory({
    root: '/'
})`"></Code>
        <H3></H3>
        <Instructions :data="[{
            name: 'gobackName',
            type: 'String',
            default: '\'go back\'',
            describe: '',
        }, {
            name: 'root',
            type: 'String',
            default: '\'/\'',
            describe: '',
        }, {
            name: 'insertRoot',
            type: 'Boolean',
            default: 'true',
            describe: '',
        }]"></Instructions>

        <H2>Location</H2>
        <P>window.location</P>
        <Instructions type="h3" :data="[{
            name: 'hash',
            type: 'string',
            describe: 'hashwidow.location.hash',
        }, {
            name: 'pathname',
            type: 'string',
            describe: 'widow.location.pathname',
        }, {
            name: 'search',
            type: 'string',
            describe: 'widow.location.search',
        }, {
            name: 'key',
            type: 'string',
            describe: 'idhreflocation',
        }, {
            name: 'href',
            type: 'string',
            describe: 'url',
        }]"></Instructions>

        <H2>MyHistory</H2>
        <H3>MyHistory.prototype.push(path: string, state?: any): Promise<{{'Location>'}}</H3>
        <Function :data="`
    /**
     * history.push
     * @param {string} path                 
     * @param {any} state                   JSON.stringify
     * @returns {Promise<Location>}        promiseILocation
     * @memberOf IHistory
     */
        `"></Function>
        <H3>MyHistory.prototype.replace(path: string, state?: any): Promise<{{'Location>'}}</H3>
        <Function :data="`
    /**
     * URLURLhistory.replace
     * @param {string} path                 
     * @param {any} state                   JSON.stringify
     * @returns {Promise<Location>}        promiseILocation
     * @memberOf IHistory
     */
        `"></Function>
        <H3>MyHistory.prototype.goback(n?: number): Promise<{{'Location>'}}</H3>
        <Function :data="`
    /**
     * history.goback
     * @param {number} n                    
     * @returns {Promise<Location>}        promiseILocation
     * @memberOf IHistory
     */
        `"></Function>
        <H3>MyHistory.prototype.goback(path: string): Promise<{{'Location>'}}</H3>
        <Function :data="`
    /**
     * pathpathroot
     * @param {string} path                 path
     * @returns {Promise<Location>}        promiseILocation
     * @memberOf IHistory
     */
        `"></Function>
        <H3>MyHistory.prototype.goback(fn: (fn: ReadonlgLocation)=>boolean): Promise<{{'Location>'}}</H3>
        <Function :data="`
    /**
     * locationpathroot
     * @param {(fn: ReadonlgLocation)=>boolean} fn      
     * @returns {Promise<Location>}        promiseILocation
     * @memberOf IHistory
     */
        `"></Function>
        <H3>MyHistory.prototype.reload(): Promise<{{'Location>'}}</H3>
        <Function :data="`
    /**
     * location.reload
     * @returns {Promise<Location>}        promiseILocation
     * @memberOf IHistory
     */
        `"></Function>
        <H3>MyHistory.prototype.destroy(): Promise<{{'void>'}}</H3>
        <Function :data="`
    /**
     * 
     * @memberOf IHistory
     */
        `"></Function>

        <H2>MyHistory</H2>
        <Instructions type="h3" :data="[{
            name: 'stack',
            type: 'Location[]',
            describe: 'Location',
        }, {
            name: 'length',
            type: 'number',
            describe: 'Locationhistory.length',
        }, {
            name: 'location',
            type: 'Location',
            describe: 'ILocation',
        }, {
            name: 'isBusy',
            type: 'boolean',
            describe: 'falsepushreplacegoback',
        }]"></Instructions>

        <H2></H2>
        <H3>onChange</H3>
        <Function :data="`
    /**
     * URLchange
     *  @param {'init' | 'push' | 'goback' | 'replace' | 'reload'}    action URL
     *  @param {Location}    oldLoction loction
     *  @param {Location}    newLoction location
     *  @param {Location[]}    discardLoctions location
     *  @param {Location[]}    includeLoctions location
     * @returns {void | Promise<void>}        Promise<void>
     * @memberOf IHistory
     */
        `"></Function>
        <P>action'init' | 'push' | 'goback' | 'replace' | 'reload'</P>
        <Li>init</Li>
        <Li>pushpush</Li>
        <Li>gobackgoback</Li>
        <Li>replacereplace</Li>
        <Li>reloadreload</Li>
        <Function :data="`
    /**
     * BeforeURLURLchangehistory
     * BeforeChange,
     * 
     *  @param {'init' | 'push' | 'goback' | 'replace' | 'reload'}    action URL
     *  @param {Location}    oldLoction loction
     *  @param {Location}    newLoction location
     *  @param {Location[]}    discardLoctions location
     *  @param {Location[]}    includeLoctions location
     * @returns {boolean | Promise<boolean>}        Promise<boolean>booleanfalsefalsefalsy
     * @memberOf IHistory
     */
        `"></Function>
    </Doc>
</template>
<script>
import {MyHistory} from 'my-router-history'

export default {
    data(){
        return {
        }
    },
}
</script>



